<template>
	<view class="">
		<view class="title">
			<!-- #ifdef APP-PLUS -->
			<uni-status-bar bgcolor="transparent"></uni-status-bar>
			<!-- #endif -->
			<view class="title-text">
				{{i18n.Hash_card}}
			</view>
			<view class="title-info">
				<view class="item" :class="{'active':index==0}" @tap.stop="selectnav('0')">{{i18n.Unused}}</view>
				<view class="item" :class="{'active':index==1}" @tap.stop="selectnav('1')">{{i18n.Used}}</view>
				<view class="item" :class="{'active':index==2}" @tap.stop="selectnav('2')">{{i18n.Received}}</view>
				<view class="item" :class="{'active':index==3}" @tap.stop="selectnav('3')">{{i18n.Transferred}}</view>
			</view>
		</view> 
		<view class="content">
			<template v-if="index==0">
				<view class="list" :class="{'animated bounceInUp fast':index==0}">
					<image src="../../static/image/hash-card.png" mode="widthFix" lazy-load></image>
					<view class="list-item">
						<view class="items-l">
							{{i18n.quantity}}  {{info.cash}}{{i18n.piece}}
						</view>
						<view class="items-r">
							<view @tap.stop="sendCard">{{i18n.To_transfer}}</view>
							<view @tap.stop="jumpBuy">{{i18n.To_use}}</view>
						</view>
					</view>
				</view>
			</template>
			<template v-if="(index==1&& list.length>0) || (index==2&& list.length>0) || (index==3&& list.length>0) ">
				<block v-for="(item,index) in list" :key="index">
					<view class="list list-used" :class="{'animated bounceInUp fast':index==1}">
						<image src="../../static/image/hash-card.png" mode="widthFix" lazy-load></image>
						<text class="time">{{item.time}}</text>
						<view class="list-item">
							<view class="items-l">
								{{i18n.quantity}}  {{item.num}}{{i18n.piece}}
							</view>
							<view class="items-r">
								<view>{{item.typeStr}}</view>
							</view>
						</view>
					</view>
				</block>
			</template>
			
		</view>
		
		<load v-if="laoding"></load>
	</view>
</template>

<script>
	import uniStatusBar from '@/components/uni-status-bar/uni-status-bar.vue';
	import {pageto, showToast, fetch, forceUpdate} from '@/common/js/util.js';
	import Load from '@/components/common/load.vue';
	import {mapGetters, mapMutations} from 'vuex' 
	export default {
		components:{
			uniStatusBar,Load
		},
		data() {
			return {
				laoding:false,
				index:0,
				page:1,
				info:{},
				type:'',
				list:[],
				isCheck:false
			}
		},
		computed:{
			i18n() {
				return this.$t('message')
			},
			...mapGetters([
				'getRequestUrl',
				'getLangType',
				'getLogin',
				'getLoginInfo'
			]),
		},
		onPullDownRefresh() {
					//下拉刷新的时候请求一次数据
					// console.log('下拉了')
			this.page =1;
			this.list =[]
			if(this.index=='1'){
				this.getUseList();
				return false;
			}
		    this.getList();
		},
		onReachBottom() {
			let _self = this
				//触底的时候请求数据，即为上拉加载更多
				//为了更加清楚的看到效果，添加了定时器
		    if (timer != null) {
		        clearTimeout(timer);
		    }
		    timer = setTimeout(function() {
				if(_self.index=='1'){
					_self.getUseList();
					return false;
				}
		        _self.getList();
		    }, 1000);
					
					// 正常应为:
					// _self.getmorenews();
		},
		onLoad(){
			this.page =1;
			this.list =[]
			this.getList();
		},
		onShow() {
			forceUpdate(this.getLangType);
		},
		methods:{
			getUseList(){
				let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token,
					page:this.page,
					rows:10
				}
				if(this.isCheck) return;
				this.isCheck = true;
				fetch('/api/wallet/djqUseRecord',data,'post')
					.then(res=>{
						this.info = res.data.data;
						this.isCheck = false;
						if(res.data.data.list.data.length==0) {
							showToast(this.i18n.nodata)
							uni.hideNavigationBarLoading();
							return
						}
						if(res.data.code){
							if(this.page==1){
								this.page++;
								uni.stopPullDownRefresh();
								this.list = res.data.data.list.data
							}else{
								this.page++;
								this.list= this.list.concat(res.data.data.list.data)
							}
						}
					})
					.catch(err=>{
						this.isCheck = false;
					})
			},
			getList(){
				let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token,
					page:this.page,
					rows:10,
					type:this.type
				}
				if(this.isCheck) return;
				this.isCheck = true;
				fetch('/api/wallet/djqRecord',data,'post')
					.then(res=>{
						this.info = res.data.data;
						this.isCheck = false;
						if(res.data.data.list.data.length==0) {
							showToast(this.i18n.nodata)
							uni.hideNavigationBarLoading();
							return
						}
						if(res.data.code){
							if(this.page==1){
								this.page++;
								uni.stopPullDownRefresh();
								this.list = res.data.data.list.data
							}else{
								this.page++;
								this.list= this.list.concat(res.data.data.list.data)
							}
						}
					})
					.catch(err=>{
						this.isCheck = false;
					})
			},
			selectnav(val){
				this.index=val;
				if(val=='2'){
					this.type=44;
					this.page =1;
					this.list =[]
					this.getList();
				}else if(val=='3'){
					this.type=43;
					this.page =1;
					this.list =[]
					this.getList();
				}else if(val=='1'){
					this.type=40
					this.page =1;
					this.list =[]
					this.getUseList();
				}
				
			},
			sendCard(){
				uni.navigateTo({
					url:`/pages/hash-card/send-card?info=${JSON.stringify(this.info)}`
				})
			},
			jumpBuy(){
				uni.navigateTo({
					url:'/pages/application/computing-power'
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '@/common/scss/variable.scss';
	.title{
		background: #1D184F;
		color: $white;
		.title-text{
			line-height: 78upx;
			text-align: center;
		}
		.title-info{
			padding: 48upx 0 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.item{
				flex: 1;
				padding-bottom: 20upx;
				text-align: center;
				font-size: 34upx;
				color: $white;
				position: relative;
				&.active{
					color: #5631FD;
					&::after{
						content: "";
						position: absolute;
						left: 50%;
						bottom: 0;
						transform: translateX(-50%);
						height: 3upx;
						width: 160upx;
						background-color: #5631FD;
					}
				}
			}
		}
	}
	.content{
		padding: 20upx 30upx;
		.list{
			position: relative;
			padding-top: 200upx;
			width: 690upx;
			height: 300upx;
			.time{
				position: absolute;
				left: 42upx;
				top: 116upx;
				font-size: 26upx;
				color: $white;
			}
			&.list-end{
				.list-item{
					.items-r{
						view{
							&:first-of-type{
								padding-right: 0;
								&::after{
									display: none;
								}
							}
							&:last-of-type{
								margin-left: 0;
							}
						}
					}
				}
			}
			&.list-used{
				.list-item{
					.items-r{
						view{
							&:first-of-type{
								padding-right: 0;
								text{
									margin-left: 15upx;
								}
								&::after{
									display: none;
								}
							}
							&:last-of-type{
								margin-left: 0;
							}
						}
					}
				}
			}
			image{
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				bottom: 0;
				z-index: -1;
				flex-shrink: 0;
				width: 690upx;
				height: 300upx;
			}
			.list-item{
				display: flex;
				justify-content: space-between;
				align-items: center;
				line-height: 100upx;
				color: #0F0C39;
				padding: 0 54upx 0 40upx;
				font-size: 30upx;
				.items-r{
					display: flex;
					align-items: center;
					view{
						&:first-of-type{
							padding-right: 40upx;
							position: relative;
							&::after{
								content:"";
								position: absolute;
								right: 0;
								bottom: 50%;
								height: 30upx;
								width: 2upx;
								background-color: #2B2860;
								transform: translateY(50%);
							}
						}
						&:last-of-type{
							margin-left: 40upx;
						}
					}
				}
			}
		}
	}
</style>
